export default {
	data() {
		return {}
	},
	onLoad(res) {
	},
	onShow(res) {
		this.getTicketPaymentInfo(res.orderNum);
	},
	onReady() {
		this.backHome();
	},
	methods: {
		//--------------------------获取车票支付参数--------------------------
		getTicketPaymentInfo: function(res) {
			var that = this;
			var timer=null;
			that.timer = timer;
			timer=setInterval(function(){
				uni.request({
					url: 'http://zntc.145u.net/api/ky/SellTicket_Flow',
					method: 'GET',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						orderNumber: res.orderNum
					},
					success: (res) => {
						console.log(res.data);
						if (res.data != null) {
							if (res.data) {
								var msgArray = JSON.parse(res.data.msg);
								if(msgArray.msg == '订票失败') {
									uni.showToast({
										title: msgArray.msg,
										icon: 'none'
									})
									clearInterval(timer);
								}
							}
						}
					},
					fail(res) {
						uni.hideLoading();
						console.log('失败');
						//回调失败，取消定时器
						clearInterval(timer);
					}
				})
			}, 3000)
		},
		//路由统一事件
		godetail: function(e) {
			if(e==0){
				uni.switchTab({
					url: '../../../order/OrderList'
				});
			}else if(e==1){
				uni.switchTab({
					url: '../../../Home/Index'
				});
			}
		},
		backHome : function() {
			setTimeout(() => {
				uni.switchTab({
					url: '../../../Home/Index'
				});
			}, 10000);
		}

	}
}
